import React, {useCallback, useEffect, useState} from 'react';
import {Switch} from 'react-router-dom';
import styled from 'styled-components';
import Page from "../../components/Page";
import useTiTICoreContracts from "../../hooks/useTiTICoreContracts";
import Spacer from "../../components/Spacer";
import useReOrders from "../../hooks/useReOrders";
import usdc from "../../assets/img/icon/usdc.svg";
import dai from "../../assets/img/icon/dai.svg";
import eth from "../../assets/img/icon/eth.svg";
import wbtc from "../../assets/img/icon/wbtc.svg";
import usdcTiTi from "../../assets/img/icon/usdcD.svg";
import daiTiTi from "../../assets/img/icon/daiD.svg";
import ethTiTi from "../../assets/img/icon/wethD.svg"
import wbtcTiTi from "../../assets/img/icon/wbtcD.svg";

const Dashboard: React.FC = () => {
    const beiCash = useTiTICoreContracts();
    const [pegPrice, setPegPrice] = useState(0);
    const [titiSupply, setTitiSupply] = useState(0);
    const [titiCirculation, setTitiCirculation] = useState(0);
    const [titiSwapDepth, setTitiSwapDepth] = useState(0);
    const [usdcSwapDepth, setUSDCSwapDepth] = useState(0);
    const [prv, setPRV] = useState(0);
    const [mmf, setMMF] = useState(0);
    const [rainyDayFund, setRainyDayFund] = useState(0);
    const [reserveRatio, setReserveRatio] = useState(0);

    const { onReOrders } = useReOrders();

    const fetchStats = useCallback(async () => {
        let [dPegPrice, dTitiSupply, dReserve, dMMF, dRainyDayFund, dTitiCirculation, dPRV] = await Promise.all([
            beiCash.getPegPrice(),
            beiCash.getTITISupply(),
            beiCash.getTITIUSDCReserves(),
            beiCash.getMMF(),
            beiCash.getRainyDayFund(),
            beiCash.getTitiCirculation(),
            beiCash.getPRV(),
        ]);

        let dReserveRatio = 0;
        //let dFundRatio = 0;
        dTitiSupply = dTitiSupply - 100
        let dTitiSwapDepth = dReserve[1]
        let dUSDCSwapDepth = dReserve[0]
        if (dTitiCirculation > 0) {
            dReserveRatio = 100 * (dPRV) / dTitiCirculation
            //dFundRatio = 100 * (dMMF - 1000000) / dTitiCirculation
        }
        console.log(dReserveRatio)
        setPegPrice(dPegPrice);
        setTitiSupply(dTitiSupply);
        setTitiCirculation(dTitiCirculation);
        setTitiSwapDepth(dTitiSwapDepth);
        setUSDCSwapDepth(dUSDCSwapDepth);
        setPRV(dPRV);
        setMMF(dMMF);
        setRainyDayFund(dRainyDayFund);
        setReserveRatio(dReserveRatio);

    }, [beiCash]);

    useEffect(() => {
        if (beiCash) {
            fetchStats().catch((err) => console.error(err.stack));
        }
    }, [beiCash, fetchStats]);

    return (
        <Switch>
            <Page>
                <StyledMain>
                    <div style={{padding: "10px"}}/>
                    <LPCardWrapper bgColor="#262729" shadowColor="#D3D1CD" width="60%">
                        <ImageRowWrapper>
                            <div style={{fontSize: "20px", padding: "6px 5px 1px", width: "60%"}}>
                                TiTi Info
                            </div>
                        </ImageRowWrapper>
                        <TotalRowWrapper>
                            <TextRowWrapper width="88%">
                                <div style={{fontSize: "14px", padding: "4px 0px 4px"}}>Peg Price:</div>
                                <div style={{fontSize: "14px", padding: "4px 0px 4px", color: "#34E2C9"}}>$1.000</div>
                            </TextRowWrapper>
                            <TextRowWrapper width="88%">
                                <div style={{fontSize: "14px", padding: "4px 0px 4px"}}>TiTi Circulation:</div>
                                <div style={{fontSize: "14px", padding: "4px 0px 4px", color: "#34E2C9"}}>100,003,067 TiTi</div>
                                <Spacer size="sm"/>
                                <div style={{fontSize: "14px", padding: "4px 0px 4px"}}>TiTi in M-AMMs:</div>
                                <div style={{fontSize: "14px", padding: "4px 0px 4px", color: "#34E2C9"}}>129,173,474 TiTi</div>
                            </TextRowWrapper>
                            <TextRowWrapper width="88%">
                                <div style={{fontSize: "14px", padding: "4px 0px 4px"}}>TiTi Total Supply:</div>
                                <div style={{fontSize: "14px", padding: "4px 0px 4px", color: "#34E2C9"}}>229,176,541 TiTi</div>
                            </TextRowWrapper>
                        </TotalRowWrapper>
                    </LPCardWrapper>
                    <LPCardWrapper bgColor="#262729" shadowColor="#D3D1CD" width="60%">
                        <ImageRowWrapper>
                            <div style={{fontSize: "20px", padding: "6px 5px 1px", width: "60%"}}>
                                M-AMMs Info
                            </div>
                        </ImageRowWrapper>
                        <TotalRowWrapper>
                            <TextRowWrapper width="88%">
                                <div style={{fontSize: "18px", paddingBottom: "8px", color: "#FFF", fontWeight: 600, textAlign: "center"}}>
                                    <img src={usdcTiTi} alt="bei" height="50px" />
                                </div>
                                <div style={{fontSize: "14px", padding: "4px 0px 4px", color: "#268FEF"}}>USDC: 37,293,096</div>
                                <div style={{fontSize: "14px", color: "#268FEF"}}>TiTi: 37,367,831</div>
                            </TextRowWrapper>
                            <TextRowWrapper width="88%">
                                <div style={{fontSize: "18px", paddingBottom: "8px", color: "#FFF", fontWeight: 600, textAlign: "center"}}>
                                    <img src={daiTiTi} alt="bei" height="50px" />
                                </div>
                                <div style={{fontSize: "14px", padding: "4px 0px 4px", color: "#268FEF"}}>DAI: 28,353,748</div>
                                <div style={{fontSize: "14px", color: "#268FEF"}}>TiTi: 27,421,419</div>
                            </TextRowWrapper>
                            <TextRowWrapper width="88%">
                                <div style={{fontSize: "18px", paddingBottom: "8px", color: "#FFF", fontWeight: 600, textAlign: "center"}}>
                                    <img src={ethTiTi} alt="bei" height="50px" />
                                </div>
                                <div style={{fontSize: "14px", padding: "4px 0px 4px", color: "#268FEF"}}>WETH: 16,899</div>
                                <div style={{fontSize: "14px", color: "#268FEF"}}>TiTi: 42,978,128</div>
                            </TextRowWrapper>
                            <TextRowWrapper width="88%">
                                <div style={{fontSize: "18px", paddingBottom: "8px", color: "#FFF", fontWeight: 600, textAlign: "center"}}>
                                    <img src={wbtcTiTi} alt="bei" height="50px" />
                                </div>
                                <div style={{fontSize: "14px", padding: "4px 0px 4px", color: "#268FEF"}}>WBTC: 622</div>
                                <div style={{fontSize: "14px", color: "#268FEF"}}>TiTi: 21,406,096</div>
                            </TextRowWrapper>
                        </TotalRowWrapper>
                    </LPCardWrapper>
                    <LPCardWrapper bgColor="#262729" shadowColor="#D3D1CD" width="60%">
                        <ImageRowWrapper>
                            <div style={{fontSize: "20px", padding: "6px 5px 1px", width: "60%"}}>
                                PRV Info
                            </div>
                        </ImageRowWrapper>
                        <TotalRowWrapper>
                            <TextRowWrapper width="88%">
                                <div style={{fontSize: "14px", padding: "4px 0px 4px"}}>Total PRV:</div>
                                <div style={{fontSize: "14px", padding: "4px 0px 4px", color: "#C6227C"}}>$ 129,693,874</div>
                                <Spacer />
                                <div style={{fontSize: "14px", padding: "4px 0px 4px"}}>Reserve Ratio:</div>
                                <div style={{fontSize: "14px", padding: "4px 0px 4px", color: "#C6227C"}}>129.87 %</div>
                            </TextRowWrapper>
                            <TextRowWrapper width="88%">
                                <div style={{fontSize: "18px", paddingBottom: "8px", color: "#FFF", fontWeight: 600}}>
                                    <img src={usdc} alt="bei" height="50px" />
                                </div>
                                <div style={{fontSize: "14px", padding: "4px 0px 4px"}}>USDC:</div>
                                <div style={{fontSize: "14px", padding: "4px 0px 4px", color: "#C6227C"}}>229,176,541</div>
                            </TextRowWrapper>
                            <TextRowWrapper width="88%">
                                <div style={{fontSize: "18px", paddingBottom: "8px", color: "#FFF", fontWeight: 600}}>
                                    <img src={dai} alt="bei" height="50px" />
                                </div>
                                <div style={{fontSize: "14px", padding: "4px 0px 4px"}}>DAI:</div>
                                <div style={{fontSize: "14px", padding: "4px 0px 4px", color: "#C6227C"}}>22,323,618</div>
                            </TextRowWrapper>
                            <TextRowWrapper width="88%">
                                <div style={{fontSize: "18px", paddingBottom: "8px", color: "#FFF", fontWeight: 600}}>
                                    <img src={eth} alt="bei" height="50px" />
                                </div>
                                <div style={{fontSize: "14px", padding: "4px 0px 4px"}}>WETH:</div>
                                <div style={{fontSize: "14px", padding: "4px 0px 4px", color: "#C6227C"}}>12,703</div>
                            </TextRowWrapper>
                            <TextRowWrapper width="88%">
                                <div style={{fontSize: "18px", paddingBottom: "8px", color: "#FFF", fontWeight: 600}}>
                                    <img src={wbtc} alt="bei" height="50px" />
                                </div>
                                <div style={{fontSize: "14px", padding: "4px 0px 4px"}}>WBTC:</div>
                                <div style={{fontSize: "14px", padding: "4px 0px 4px", color: "#C6227C"}}>436</div>
                            </TextRowWrapper>
                        </TotalRowWrapper>
                    </LPCardWrapper>
                    <LPCardWrapper bgColor="#262729" shadowColor="#D3D1CD" width="60%">
                        <ImageRowWrapper>
                            <div style={{fontSize: "20px", padding: "6px 5px 1px", width: "60%"}}>
                                Rainy Day Fund
                            </div>
                        </ImageRowWrapper>
                        <TotalRowWrapper>
                            <TextRowWrapper width="88%">
                                <div style={{fontSize: "14px", padding: "4px 0px 4px"}}>Total RDF:</div>
                                <div style={{fontSize: "14px", padding: "4px 0px 4px", color: "#E59F39"}}>$ 15,347,678</div>
                            </TextRowWrapper>
                            <TextRowWrapper width="88%">
                                <div style={{fontSize: "18px", paddingBottom: "8px", color: "#FFF", fontWeight: 600}}>
                                    <img src={usdc} alt="bei" height="50px" />
                                </div>
                                <div style={{fontSize: "14px", padding: "0px 0px 4px"}}>USDC</div>
                                <div style={{fontSize: "14px", padding: "4px 0px 4px", color: "#E59F39"}}>3,478,327</div>
                            </TextRowWrapper>
                            <TextRowWrapper width="88%">
                                <div style={{fontSize: "18px", paddingBottom: "8px", color: "#FFF", fontWeight: 600}}>
                                    <img src={dai} alt="bei" height="50px" />
                                </div>
                                <div style={{fontSize: "14px", padding: "0px 0px 4px"}}>DAI</div>
                                <div style={{fontSize: "14px", padding: "4px 0px 4px", color: "#E59F39"}}>2,734,765</div>
                            </TextRowWrapper>
                            <TextRowWrapper width="88%">
                                <div style={{fontSize: "18px", paddingBottom: "8px", color: "#FFF", fontWeight: 600}}>
                                    <img src={eth} alt="bei" height="50px" />
                                </div>
                                <div style={{fontSize: "14px", padding: "0px 0px 4px"}}>WETH</div>
                                <div style={{fontSize: "14px", padding: "4px 0px 4px", color: "#E59F39"}}>1971</div>
                            </TextRowWrapper>
                            <TextRowWrapper width="88%">
                                <div style={{fontSize: "18px", paddingBottom: "8px", color: "#FFF", fontWeight: 600}}>
                                    <img src={wbtc} alt="bei" height="50px" />
                                </div>
                                <div style={{fontSize: "14px", padding: "0px 0px 4px"}}>WBTC</div>
                                <div style={{fontSize: "14px", padding: "4px 0px 4px", color: "#E59F39"}}>120</div>
                            </TextRowWrapper>
                        </TotalRowWrapper>
                    </LPCardWrapper>
                </StyledMain>
            </Page>
        </Switch>
    )
};

interface CardWrapperProps {
    bgColor: string,
    shadowColor: string,
    width: string
}

const LPCardWrapper = styled.div<CardWrapperProps>`
    margin-top: 10px;
    margin-bottom: 2px;
    padding: 20px 0px 20px;
    display: flex;
    width: ${props => props.width};
    flex: 1;
    align-items: center;
    background-color: ${props => props.bgColor};
    border-radius: 12px;
    box-shadow: 0px 0px 0px 0px ${props => props.shadowColor};
    @media (max-width: 768px) {
        width: 100%;
        flex-flow: column nowrap;
        align-items: center;
        flex: 1;
        flex-direction: column;
    }
`;

const StyledMain = styled.div`
    margin-top: 0px;
    margin-bottom: 10px;
    display: flex;
    justify-content: center;
    width: 100%;
    flex-direction: column;
    align-items: center;
    text-align: center;
    @media (max-width: 768px) {
        width: 80%;
        flex-flow: column nowrap;
        align-items: center;
        flex: 1;
        flex-direction: column;
    }
`

const ImageRowWrapper = styled.div`
    position: relative;
    margin-top: 10px;
    margin-bottom: 10px;
    flex: 1;
    display: flex;
    justify-content: left;
    flex-direction: column;
    align-items: center;
    text-align: left;
    width: 88%;
    color: #FFF;
    @media (max-width: 768px) {
        width: 100%;
        flex-flow: column nowrap;
        align-items: center;
        flex-direction: column;
    }
`;

interface TextRowWrapperProps {
    width: string
}

const TotalRowWrapper = styled.div`
    justify-content: space-between;
    align-items: center;
    flex: 3;
    display: flex;
`;

const TextRowWrapper = styled.div<TextRowWrapperProps>`
    position: relative;
    margin-top: 10px;
    margin-bottom: 10px;
    justify-content: space-between;
    flex-direction: column;
    align-items: center;
    text-align: center;
    width: ${props => props.width};
    color: #A4A4A4;
    @media (max-width: 768px) {
        width: 100%;
        flex-flow: column nowrap;
        align-items: center;
        flex-direction: column;
    }
`;

export default Dashboard;
