import { useCallback } from 'react';
import useTiTICoreContracts from './useTiTICoreContracts';
import useHandleTransactionReceipt from './useHandleTransactionReceipt';

const useReOrders = () => {
    const beiCash = useTiTICoreContracts();
    const handleTransactionReceipt = useHandleTransactionReceipt();

    const handleReOrders = useCallback(() => {
        handleTransactionReceipt(beiCash.reorders(), `ReOrders`);
    }, [beiCash, handleTransactionReceipt]);

    return { onReOrders: handleReOrders };
};

export default useReOrders;
